body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
body {
  background: #f1f1f1;
}
.loader {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  color: #323232;
  padding: 10px 10px 20px;
}
.info {
  padding: 40px 30px 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.info__title {
  font-size: 30px;
  font-weight: bold;
  color: #323232;
  margin-bottom: 10px;
}
.info__text {
  font-size: 16px;
  color: #6f6f6f;
}
.container {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-left: 15px;
  margin-right: 15px;
}
.block {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  align-self: flex-start;
  margin-bottom: 30px;
  background: white;
  border-radius: 3px;
  border: 1px solid f1f1f1;
}
@media screen and (max-width: 1199px) {
  .block {
    width: calc(33.333333333333336% - 30px);
  }
}
@media screen and (max-width: 991px) {
  .block {
    width: calc(50% - 30px);
  }
}
@media screen and (max-width: 767px) {
  .block {
    width: calc(100% - 30px);
  }
}
.block__img {
  width: 200%;
  height: auto;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.block__info {
  display: flex;
  flex-direction: column;
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
}
.block__title {
  font-size: 18px;
  font-weight: bold;
  color: #323232;
  margin-bottom: 10px;
}
.block__text {
  font-size: 16px;
  line-height: 21px;
  color: #505050;
  flex-grow: 1;
}
.block__text_dark {
  color: #1f1f1f;
}
